<template>
  <div>
    <v-container style="margin-top: 25px">
      <v-row align="center" justify="center">
        <v-col lg="12" md="12" sm="12">
          <v-card>
            <v-system-bar color="primary"></v-system-bar>
            <v-card-text class="my-4 text-center title">
              <span style="text-shadow: 1px 1px 1px black; margin-right: 5px">
                REVISTAS EDUCATIVAS
              </span>
            </v-card-text>
            <v-container
              class="mx-auto"
              max-width="100%"
              style="margin-top: 30px; margin-bottom: 40px"
              color="primary"
            >
              <v-expansion-panels
                v-for="(contA, i) in papers"
                :key="i"
                :v-model="i"
              >
                <v-expansion-panel>
                  <v-expansion-panel-header>
                    <h3 align="center" justify="center"></h3>
                    <v-toolbar-title>
                      <v-card-title>
                        <span style="text-shadow: 1px 1px 1px black">
                          REVISTA {{ i + 1 }}</span
                        >
                      </v-card-title>
                    </v-toolbar-title>
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <v-card-title>
                      <span style="text-shadow: 1px 1px 1px black">
                        TITULO:
                      </span>
                      <span class="ml-1">
                        {{ contA.title }}
                      </span>
                    </v-card-title>
                    <v-card-title>
                      <span style="text-shadow: 1px 1px 1px black"> AÑO: </span>
                      <span class="ml-1">
                        {{ contA.age }}
                      </span>
                    </v-card-title>
                    <v-card-title>
                      <span style="text-shadow: 1px 1px 1px black">
                        AUTORES:
                      </span>
                    </v-card-title>
                    <ul>
                      <li
                        v-for="(contB, j) in contA.authors"
                        :key="j"
                        class="text-justify title"
                      >
                        {{ contB.name }}
                      </li>
                    </ul>
                    <v-card-title>
                      <span style="text-shadow: 1px 1px 1px black">
                        RESUMEN
                      </span>
                    </v-card-title>
                    <v-card-text
                      v-for="(contC, k) in contA.paragraph"
                      :key="k"
                      class="text-justify title"
                    >
                      {{ contC.text }}
                    </v-card-text>
                    <v-card-text
                      class="text-justify title"
                    
                    >

                                          <a :href=contA.url target="_blank">
                           Leer artículo completo.
                      </a>
                    </v-card-text>
                    <v-card>
                      <v-img
                        :src="contA.img"
                        height="100%"
                        class="grey darken-4"
                      ></v-img>
                    </v-card>
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
            </v-container>
          </v-card>
        </v-col>
      </v-row>
    </v-container>

    <Footer></Footer>
  </div>
</template>
<script>
import Footer from "@/components/Default/Footer";

export default {
  components: {
    Footer,
  },
  Valoresname: "Papers",
  data() {
    return {
      panel: [],
      panel1: 0,
      panel2: 0,
      items: 1,
      papers: [
        {
          title:
            "Mundos virtuales y el aprendizaje inmersivo en educación superior.",
          age: "2020.",
          authors: [
            { name: "Rocío Janett Ayala Pezzutti." },
            { name: "Carlos Miguel Laurente Cárdenas." },
            { name: "César Daniel Escuza Mesías." },
            { name: "Luis Alberto Núñez Lira." },
            { name: "Jorge Rafael Díaz Dumont." },
],
          url: "http://www.scielo.org.pe/pdf/pyr/v8n1/2310-4635-pyr-8-01-e430.pdf",
          img: require("@/assets/images/papers/paper1.jpg"),
          paragraph: [
            {
              text:
                "La sociedad actual se ha convertido en un oscilar constante de cambios en cada aspecto de la vida;\n" +
                "académico, económico, cultural, político, entre otros. Los cambios se han generado con la inserción \n" +
                "de tecnología en las actividades diarias, donde el aprendizaje no queda exento a estos cambios. Según \n" +
                "Negroponte (1995), cuando la ciencia se une con la tecnología esta produce cambios significativos \n" +
                "en nuestra vida, así como la forma como entendemos la realidad. Hoy la sociedad del conocimiento \n" +
                "está potenciada por la tecnología y por la creación de nuevas y diversas formas de interactuar como \n" +
                "personas; los mundos virtuales que son una representación gráfica de la realidad diseñada en 3D, es \n" +
                "una de ellas. Second Life es uno de estos mundos virtuales, convirtiéndose de un sitio de mero \n" +
                "entretenimiento y vida social en la web, a establecerse en ambiente que fomenta la creatividad, el \n" +
                "trabajo colaborativo, la visión empresarial, el desarrollo de habilidades tecnológicas, el desarrollo de \n" +
                "experiencias de aprendizaje en entornos 3D, entre otros aspectos. Asimismo, el aprendizaje inmersivo\n" +
                "posibilita la interacción en ambientes tridimensionales. Allí las actividades se realizan dentro de un \n" +
                "mundo construido artificialmente el cual asemeja la realidad, esto permite construir el aprendizaje a \n" +
                "través de las experiencias propias que tenga la persona en interacción con el entorno y las experiencias \n" +
                "entre los usuarios de este mundo virtual. Por tal razón, se propone el uso del mundo virtual Second \n" +
                "Life como una herramienta formativa en el aprendizaje inmersivo.",
            },
            {
              text: "",
            },
          ],
        },
        {
          title: "El M-Learning, las ventajas de la utilización de dispositivos móviles en el proceso autónomo de aprendizaje.",
          age: "2019.",

          authors: [{ name: "Ronald Zamora Delgado" }],
          img: require("@/assets/images/papers/paper2.jpg"),
          url: "https://dialnet.unirioja.es/descarga/articulo/7047179.pdf",

          paragraph: [
            {
              text:"El avance de la tecnología móvil ha generado en la actualidad una mejora en la educación para apoyar \n" +
                "a los docentes y mejorar sus prácticas de enseñanza tanto dentro como fuera del aula de clases. El \n" +
                "empleo de los dispositivos móviles permite reforzar el proceso de enseñanza y aprendizaje sin \n" +
                "importar las limitaciones del tiempo y espacio. Por ello, a lo largo de este trabajo se hará hincapié en \n" +
                "las concepciones y estrategias metodológicas que pueden aplicarse a partir del aprendizaje móvil, \n" +
                "llamado también M-Learning, para fortalecer el aprendizaje habitual y mejorar al trabajo educativo \n" +
                "del profesor, de tal forma que se permita promover un aprendizaje autónomo en relación con el avance \n" +
                "de las nuevas tecnologías de la información y la comunicación. Se parte del hecho de que se \n" +
                "encuentran disponibles una diversidad de aplicaciones y estrategias para usar en dispositivos móviles \n" +
                "que propicien un mejor manejo de los recursos tecnológicos disponibles. En este sentido, el docente \n" +
                "debe considerar la metodología y actividades que pretende ejecutar con el empleo de las prácticas del \n" +
                "M-Learning y configurar una temática en particular de una disciplina con la auténtica esencia de lo \n" +
                "que significa el aprendizaje móvil en los estudiantes, de lo contrario no se alcanzarán esperados. Es \n" +
                "decir que, como en toda actividad educativa, la planificación resulta fundamental. Finalmente, es \n" +
                "preciso considerar que otro de los propósitos de este trabajo es disminuir el desconocimiento que \n" +
                "tanto los maestros como los estudiantes tienen acerca de las ventajas y beneficios que ofrece el M-Learning y de cómo la aplicación de sus procedimientos puede convertirse en una provechosa \n" +
                "herramienta innovadora para obtener resultados satisfactorios en el proceso educativo\n" +
                "contemporáneo.",
            },
            {
              text: "",
            },
          ],
        },
      ],
    };
  },
  created() {},
  mounted() {
    if (this.$route) this.route = this.$route.query.redirect;
    this.all();
  },
  methods: {
    datas() {
      alert(this.panel1);
    },
    all() {
      this.panel = [...Array(this.items.length).keys()].map((k, i) => i);
    },
  },
};
</script>

